import React from 'react'
import './Banner.css'

import ad2 from './images/ad-2.png'


const Banner = () => (
  <div className="header-ads">
    <a href="https://swap.online">
      <img src={ad2} className="center-block" alt="Logo" />
    </a>
  </div>
)

export default Banner



